.fantasyMatch {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-template-areas: 'homeClub homeScore awayClub awayScore' 'matchDate ........ ........ .........'; }
  .fantasyMatch .homeClub {
    grid-area: homeClub;
    text-align: center; }
  .fantasyMatch .homeScore {
    grid-area: homeScore;
    text-align: center; }
  .fantasyMatch .awayClub {
    grid-area: awayClub;
    text-align: center; }
  .fantasyMatch .awayScore {
    grid-area: awayScore;
    text-align: center; }
  .fantasyMatch .matchDate {
    grid-area: matchDate;
    text-align: center; }
  .fantasyMatch .title {
    font-weight: bold; }

.hidden {
  display: none; }



